import React, {memo} from 'react';
import ListItem from './ListItem';
import LinkUrl from './LinkUrl';
import SectionHeader from './SectionHeader';


const About = memo(function About({ title, subtitle, header, buttons }) {

    return (
        <section id="about">
            <div className="about-container">
                {/* <Header title={title} subtitle={subtitle} header={header} buttons={buttons}/> */}
                <div className="main-container main-bio-container">
                    {/* <Navigation buttons={buttons} /> */}
                    <div className="bio-panel info-panel">
                    <SectionHeader text={"About me"} iconName={"about"} />
                        <p>
                            I am a software engineer based in Santa Cruz, CA
                            looking for opportunities in 2D/3D graphics, interaction design,
                            and data visualization.
                        </p>
                        <p>
                            Over the past ten years,
                            I have worked on design and data-driven technical
                            projects at <LinkUrl
                                text="NASA Jet Propulsion Laboratory"
                                url=""
                            />, <LinkUrl
                                text="The Adler Planetarium"
                                url=""
                            />, <LinkUrl
                                text="Lab En Flujo"
                                url=""
                            />, <LinkUrl
                                text="Tempus Labs"
                                url=""
                            />, and <LinkUrl
                                text="The Creative Coding Lab"
                                url=""
                            />. Collaborating with top researchers,
                            I have brought my computer graphics software
                            development expertise to create real-time
                            interactive visualizations and animations
                            cosmological simulations, sky surveys,
                            prototype spacecraft, and protein interaction
                            rules.
                        </p>
                        <p>
                            In addition to my research and engineering
                            experience, I am  a volunteer electronics
                            and lighting lead at <LinkUrl
                                text="Liminal Space Art Collective"
                                url=""
                            />,
                            was an artistic associate and stage technician
                            at <LinkUrl
                                text="Shattered Globe Theatre Company"
                                url=""
                            /> when I
                            lived in Chicago, as well as a music producer
                            with <LinkUrl
                                text="The Tapestries Collective"
                                url=""
                            />. As a Ph.D. in the <LinkUrl
                                text="Computational Media Department"
                                url=""
                            /> at UC Santa Cruz, I taught classes and lab sections
                            in Game Technologies, Game Engines, Game Graphics
                            and Real-time Rendering, Creating Digital Audio,
                            and Musical Data.
                        </p>
                        <p>
                            These days, I am creating my original game <LinkUrl
                                text="Raccoon Runner"
                                url=""
                            />. When I'm not working, I am
                            usually hanging out with my partner and our cats,
                            making music, biking around Santa Cruz,
                            or creating something at the <LinkUrl
                                text="Idea Fab Labs"
                                url=""
                            />.
                        </p>

                    </div>
                    <div className="about-right-col">
                        <div className="education-panel info-panel">
                        <SectionHeader text={"Education"} iconName={"education"} />
                            <ul>
                            <ListItem text={<p>
                                <b>University of California Santa Cruz</b> <br></br>
                                Ph.D. in Computational Media
                            </p>} />

                            <ListItem text={
                                <p>
                                    <b>DePaul University</b><br></br>
                                    M.S. Coursework in Predictive Analytics<br></br>
                                    B.S. in Biology and Physics, Magna Cum Laude<br></br>
                                </p>} />
                                </ul>
                        </div>
                        
                        <div className="image-panel info-panel">
                            <img src="images/crystal.webp" alt="Crystal" loading='lazy'/>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
})

export default About;